/**
 * –ö–æ–º–ø–æ–Ω–µ–Ω—Ç
 * - —Å–æ–¥–µ—Ä–∂–∏—Ç —Å–≤–æ–π—Å—Ç–≤–æ _mounted
 * - –∑–Ω–∞–µ—Ç, —Ä–∞–∑–º–µ—â–µ–Ω –ª–∏ –æ–Ω –≤ –æ—Å–Ω–æ–≤–Ω–æ–º –¥–µ—Ä–µ–≤–µ react –∏–ª–∏ –≤–æ –≤—Å–ø–ª—ã–≤–∞—é—â–µ–º –æ–∫–Ω–µ
 * - —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç title –æ—Å–Ω–æ–≤–Ω–æ–≥–æ —Ä–∞–∑–¥–µ–ª–∞ –∏–Ω—Ç–µ—Ä—Ñ–µ–π—Å–∞
 *
 * Created 11.01.2017
 */
import React from 'react';
import PropTypes from 'prop-types';
import MComponent from './MComponent';
export default class MDNRComponent extends MComponent {
  shouldComponentUpdate({
    _mgr,
    _meta,
    title,
    handleIfaceState
  }) {
    let res = true;
    const {
      props,
      ltitle,
      context
    } = this; // –µ—Å–ª–∏ –∏–∑–º–µ–Ω–∏–ª—Å—è –º–µ–Ω–µ–¥–∂–µ—Ä, –∏—â–µ–º –Ω–æ–≤—ã–µ –Ω–∞—Å—Ç—Ä–æ–π–∫–∏ –∫–æ–º–ø–æ–Ω–æ–≤–∫–∏

    if (props._mgr !== _mgr && this.handleManagerChange) {
      this.handleManagerChange({
        _mgr,
        _meta
      });
      res = false;
    } // –µ—Å–ª–∏ –º—ã –Ω–µ –≤ –¥–∏–∞–ª–æ–≥–µ, –º–µ–Ω—è–µ–º –∑–∞–≥–æ–ª–æ–≤–æ–∫ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è


    if (!context.dnr && ltitle && title != ltitle && !props.ignoreTitle) {
      handleIfaceState && handleIfaceState({
        component: '',
        name: 'title',
        value: ltitle
      });
      res = false;
    }

    return res;
  }

  get sizes() {
    let {
      context: {
        dnr
      },
      props: {
        width,
        height
      },
      state: {
        columns
      }
    } = this;

    if (!height) {
      height = dnr && parseInt(dnr.frameRect.height) - 2;
    }

    if (!height || height < 320) {
      height = 320;
    }

    if (!width) {
      width = dnr && parseInt(dnr.frameRect.width);
    }

    if (!width || width < 480) {
      width = 480;
    } // –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–∞—è –ø—Ä–æ–∫—Ä—É—Ç–∫–∞


    return {
      width,
      height,
      columnsWidth: columns ? columns.reduce((sum, val) => sum + val._width, 0) : width
    };
  }

}
MDNRComponent.contextTypes = {
  dnr: PropTypes.object,
  customComponents: PropTypes.object
};